
import BasicInfo from 'components/common/BasicInfo'
import ContactPart from 'components/common/ContactPart'
import WarehouseContent from 'components/common/WarehouseContent'
import MatterItem from 'components/common/MatterItem'
import FormCell from 'components/common/FormCell'
import numberComma from 'vux/src/tools/number/comma.js'

import {toFixed} from 'plugins/calc'

export default {
name: "achievementDetail",
components: {
BasicInfo,
ContactPart,
WarehouseContent,
MatterItem,
FormCell,
},
data() {
return {
item: {},
amtList: [
{
name: '材质',
value: 'MATERIAL',
}, {
name: '标准供货单价',
value: '标准供货单价',
unit: '￥',
}, {
name: '供货单价',
value: '供货单价',
unit: '￥',
}, {
name: '供货金额',
value: '供货金额',
unit: '￥',
}, {
name: '标准零售单价',
value: '标准零售单价',
unit: '￥',
}, {
name: '零售单价',
value: '零售单价',
unit: '￥',
}, {
name: '零售金额',
value: '零售金额',
unit: '￥',
}, {
name: '税价合计',
value: '税价合计',
unit: '￥',
}, {
name: '每克销售单价',
value: '每克销售单价',
unit: '￥',
},
],
}
},
filters: {
numberComma,
toFixed,
},
methods: {},
created() {
let data = sessionStorage.getItem('RFD_ACHIEVEMENT_DETAIL');
this.dataInfo = JSON.parse(data);
}
}



